import { useEffect, useState } from "react";
import Request, { ApiResponse } from "common/network";
import APIConstants from "constants/api_constants";
import { loginFailed } from "constants/error";
import { message } from "antd";
import { Order, OrderStatusCount, Pagination } from "constants/types";
import { useHistory } from "react-router-dom";
import { OrderStatus } from "constants/db_constants";
import useInputHook from "hooks/useInputHook";

export default function useManager() {
  const defaultCount: OrderStatusCount[] = [];
  const defaultPagination: Pagination<Order> = {};

  const history = useHistory();
  const searchInput = useInputHook();
  // state
  const [statusCounts, setStatusCounts] = useState(defaultCount);
  const selectedDefault: string = OrderStatus.TOTAL;
  const [statusSelected, setStatusSelected] = useState(selectedDefault);
  const [orders, setOrders] = useState(defaultPagination);
  const [rangeSelected, setRangeSeleted] = useState({
    start: "",
    end: "",
  });

  function onChangeDate(dates: any[], dateStrings: string[]) {
    // console.log("From: ", dates[0], ", to: ", dates[1]);
    // console.log("From: ", dateStrings[0], ", to: ", dateStrings[1]);
    setRangeSeleted({ start: dateStrings[0], end: dateStrings[1] });
  }
  function search() {
    // console.log("searchInput: ", searchInput.value);
    fetchData(
      rangeSelected.start,
      rangeSelected.end,
      statusSelected,
      searchInput.value
    );
  }
  function refetchOrder(status: string) {
    setStatusSelected(status);
    getOrders(
      rangeSelected.start,
      rangeSelected.end,
      status,
      searchInput.value
    );
  }

  // fetch
  useEffect(() => {
    fetchData("", "");
  }, []);

  async function fetchData(
    startDate: string,
    endDate: string,
    status?: string,
    search?: string
  ) {
    const hide = message.loading("Action in progress..", 0);
    try {
      await Promise.all([
        getStatusCount(startDate, endDate),
        getOrders(startDate, endDate, status, search),
      ]);
    } catch (_) {}
    hide();
  }
  async function getStatusCount(startDate: string, endDate: string) {
    try {
      let variables = {
        parameter: {
          language: "vi",
          startDate: startDate,
          endDate: endDate,
        },
      };
      let result = await Request(APIConstants.getStatusCount, variables);

      let res = result as ApiResponse<OrderStatusCount[]>;
      if (!res) {
        message.error("Can't parse response");
        return;
      }
      if (res.data) {
        setStatusCounts(res.data.reverse());
      }
    } catch (error) {
      message.error(loginFailed);
    }
  }
  async function getOrders(
    startDate: string,
    endDate: string,
    status?: string,
    search?: string
  ) {
    try {
      let variables = {
        parameter: {
          language: "vi",
          current: 1,
          endDate: endDate,
          ids: search,
          page: 1,
          pageSize: 20,
          size: 20,
          startDate: startDate,
          statusType: status ?? "-1",
          type: 1,
        },
      };
      let result = await Request(APIConstants.getOrders, variables);
      let res = result as ApiResponse<Pagination<Order>>;
      if (!res) {
        message.error("Can't parse response");
        return;
      }
      if (res.data) {
        setOrders(res.data);
      }
    } catch (error) {
      message.error(loginFailed);
    }
  }

  return {
    statusCounts,
    orders,
    searchInput,
    onChangeDate,
    search,
    refetchOrder,
    statusSelected,
  };
}
