import React from "react";
import { DatePicker, Space, Collapse, Input, Button, Table } from "antd";
import { ColumnsType } from "antd/es/table";

export default function CtTable({
  columns,
  datas,
  onChange,
}: {
  columns: ColumnsType;
  datas: any[];
  onChange?: any;
}) {
  return (
    <Table
      columns={columns}
      dataSource={datas}
      size="middle"
      scroll={{ x: "max-content" }}
      pagination={{
        showTotal: (total, range) => {
          return <span style={{ fontSize: "13px" }}>Tổng {total}</span>;
        },
        defaultPageSize: 10,
        pageSizeOptions: ["10", "20", "50", "100"],
        showSizeChanger: true,
      }}
    />
  );
}
