import React, { useState } from "react";
import moment from "moment";
import { DatePicker } from "antd";
const { RangePicker } = DatePicker;

export default function DateRange({
  onChange,
}: {
  onChange?: (dates: any, dateStrings: string[]) => void;
}) {
  const datesDefault: moment.Moment[] = [];
  const [dates, setDates] = useState(datesDefault);

  const disabledDate = (current: moment.Moment) => {
    if (!dates || dates.length === 0) {
      return false;
    }
    const tooLate = dates[0] && current.diff(dates[0], "days") > 30;
    const tooEarly = dates[1] && dates[1].diff(current, "days") > 30;
    return tooEarly || tooLate;
  };

  return (
    <RangePicker
      bordered
      defaultValue={[moment(), moment()]}
      disabledDate={disabledDate}
      onChange={onChange}
      onCalendarChange={(value: any) => {
        setDates(value);
      }}
      style={{
        marginRight: "16px",
        marginBottom: "12px",
      }}
      ranges={{
        "Hôm nay": [moment(), moment()],
        "Trong ba ngày": [moment().subtract(3, "day"), moment()],
        "Tuần này": [moment().startOf("week"), moment()],
        "Tháng này": [moment().startOf("month"), moment()],
        "Trong ba tháng": [moment().subtract(3, "month"), moment()],
      }}
      // dateRender={(current) => {
      //   console.log("current: ", current.date());
      //   const style: any = {};
      //   // if (current.date() === 1) {
      //   //   style.border = "1px solid #1890ff";
      //   //   style.borderRadius = "50%";
      //   // }
      //   style.border = "1px solid #1890ff";
      //   style.borderRadius = "50%";
      //   return (
      //     <div className="ant-picker-cell-inner" style={style}>
      //       {current.date()}
      //     </div>
      //   );
      // }}
      // panelRender={(current) => {
      //   return (
      //     <div>
      //       ToDay, This month
      //       {current}
      //     </div>
      //   );
      // }}
    />
  );
}
