import React from "react";

export default function ButtonTab({
  title,
  isSelected,
  onClick,
}: {
  title: string;
  isSelected?: boolean;
  onClick?: (e: any) => void;
}) {
  return (
    <span
      style={{
        color: isSelected === true ? "#f56c6c" : "#909399",
        borderBottom: isSelected
          ? "2px solid #f56c6c"
          : "2px solid transparent",
        cursor: "pointer",
        marginRight: "18px",
        marginBottom: "8px",
        fontSize: "14px",
        paddingBottom: "2px",
        width: "auto",
      }}
      onClick={onClick}
    >
      {title}
    </span>
  );
}
