import React from "react";
import "assets/css/order.css";
import { SearchOutlined } from "@ant-design/icons";
import { Collapse, Input, Button } from "antd";
import ButtonTab from "components/view/button_tab";
import DateRange from "components/view/date_range";
import { OrderStatusDisplay } from "constants/db_constants";
import useManager from "./useManager";
import CtTable from "../../../components/view/table";
import { columnOrderManager } from "./data";
const { Panel } = Collapse;

export default function OrderManage() {
  const {
    statusCounts,
    searchInput,
    onChangeDate,
    search,
    refetchOrder,
    statusSelected,
  } = useManager();

  function callback(key: any) {
    console.log(key);
  }
  function onChangeSearch(event: any) {
    let value = event.target.value as string;
    if (value === " ") {
    }
    value = value.replace(" ", ",");
    searchInput.onChange(value);
  }
  return (
    <div>
      <Collapse
        defaultActiveKey={["1"]}
        onChange={callback}
        expandIconPosition="right"
      >
        <Panel header="Điều kiện truy vấn" key="1">
          <div>
            <Input
              prefix={<SearchOutlined />}
              placeholder="Nhập số theo dõi, số điện thoại hoặc mã khách hàng"
              style={{
                width: "360px",
                marginRight: "16px",
                marginBottom: "12px",
              }}
              onChange={onChangeSearch}
              value={searchInput.value ?? ""}
            />
            <DateRange onChange={onChangeDate} />
            <Button
              type="primary"
              icon={<SearchOutlined />}
              style={{
                borderRadius: "3px",
              }}
              onClick={search}
            >
              Tra cứu
            </Button>
          </div>
          <div
            style={{
              margin: "12px 0px",
              display: "flex",
              flexWrap: "wrap",
            }}
          >
            {statusCounts.map((item) => (
              <ButtonTab
                key={"button_tab_" + item.statusName}
                title={`${OrderStatusDisplay[item.statusName]} (${
                  item.sumNumber
                })`}
                isSelected={statusSelected === item.statusName}
                onClick={() => {
                  refetchOrder(item.statusName);
                }}
              />
            ))}
          </div>
        </Panel>
      </Collapse>
      <div
        style={{
          height: "50px",
        }}
      ></div>
      <CtTable columns={columnOrderManager} datas={[]} />
    </div>
  );
}
