import { useState, useCallback } from "react";
// hook must start with use
const useInputHook = (initialValue, callback) => {
  const [value, setValue] = useState(initialValue);
  const onChange = useCallback(
    function (event, directValue) {
      if (
        event &&
        event.target &&
        event.target.value !== null &&
        event.target.value !== undefined
      ) {
        // event.targe.value maybe empty (''). van can set gia tri empty
        // window.kkLog('event.target.value:  ', event.target.value);
        // window.kkLog('event.target.name:  ', event.target.name);
        // window.kkLog('event.target.type:  ', event.target.type);

        const name = event.target.name;
        const type = event.target.type;
        let inputValue =
          type === "checkbox" ? event.target.checked : event.target.value || "";
        if (type === "number") {
          inputValue = parseInt(inputValue);
        } else if (
          typeof inputValue === "string" &&
          (inputValue.toLowerCase() === "true" ||
            inputValue.toLowerCase() === "false")
        ) {
          inputValue = inputValue.toLowerCase() === "true";
        } else if (type === "date") {
          inputValue = new Date(inputValue);
        }
        // console.log('inputValue: ', inputValue)
        if (name) {
          if (typeof callback === "function") {
            callback({ [name]: inputValue });
          } else {
            setValue({ ...value, [name]: inputValue });
          }
        } else {
          setValue(inputValue);
        }
      } else if (event && event.currentTarget && event.currentTarget.value) {
        // console.log('event.currentTarget.value: ', event.currentTarget.value);
        const name = event.currentTarget.name;
        const type = event.currentTarget.type;
        let inputValue =
          type === "checkbox"
            ? event.currentTarget.checked
            : event.currentTarget.value || "";

        if (type === "number") {
          inputValue = parseInt(value);
        } else if (
          typeof inputValue === "string" &&
          (inputValue.toLowerCase() === "true" ||
            inputValue.toLowerCase() === "false")
        ) {
          inputValue = inputValue.toLowerCase() === "true";
        }
        if (name) {
          if (typeof callback === "function") {
            callback({ [name]: inputValue });
          } else {
            setValue({ ...value, [name]: inputValue });
          }
        } else {
          setValue(inputValue);
        }
      } else if (
        event !== undefined &&
        event !== null &&
        (event instanceof Date ||
          typeof event.getMonth === "function" ||
          typeof event !== "object")
      ) {
        // window.kkLog('event :', event);
        // console.log('event is number: ', event);
        setValue(event);
      } else if (
        directValue !== undefined &&
        directValue !== null &&
        (directValue instanceof Date ||
          typeof directValue.getMonth === "function" ||
          typeof directValue !== "object")
      ) {
        // window.kkLog('directValue :', directValue);
        setValue(directValue);
      } else {
        // console.log('set value')
        setValue("");
      }
    },
    [value, callback]
  );
  return {
    value,
    onChange,
  };
};

export default useInputHook;
