import { resetLocalToken, getLocalToken } from "./local_storage";

export type ApiResponse<T> = {
  success: boolean;
  code: number;
  desc?: string;
  data?: T;
  token?: string;
};

let token = getLocalToken() ?? "";
const FETCH_TIMEOUT = 15000;

export default function Request(
  url: string,
  params: any,
  method = "POST",
  needEncodeUrl = true
) {
  if (!token) {
    token = getLocalToken() ?? "";
  }
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
    Authorization: token || "",
    // credentials: 'same-origin',
  };
  let opts: RequestInit = {
    mode: "cors",
    method,
    headers,
  };
  if (!params) {
    params = {
      token,
    };
  } else if (!params.token) {
    params.token = token;
  }
  if (method.toLowerCase() !== "get") {
    opts.body = typeof params === "object" ? JSON.stringify(params) : params;
  } else if (params) {
    let keys = Object.keys(params);
    if (keys.length > 0) {
      let query = keys.map((key) => {
        if (needEncodeUrl)
          return `${encodeURIComponent(key)}=${encodeURIComponent(
            params[key]
          )}`;
        else return `${key}=${params[key]}`;
      });
      url += `?${query.join("&")}`;
    }
  }
  let didTimeOut = false;

  return new Promise(function (resolve, reject) {
    const timeout = setTimeout(function () {
      didTimeOut = true;
      return reject(new Error("Request timed out"));
    }, FETCH_TIMEOUT);

    fetch(url, opts)
      .then(async (response) => {
        try {
          clearTimeout(timeout);
          if (!didTimeOut) {
            if (typeof response === "object") {
              if (response.status < 200 || response.status >= 300) {
                const contentType = response.headers.get("content-type") ?? "";
                const res =
                  contentType.indexOf("json") >= 0 && (await response.json());
                if (response.status === 401) {
                  resetLocalToken();
                }
                if (response.status === 503) {
                  // @ts-ignore
                  window.location = "/503";
                }
                if (response.status >= 500) {
                  return Promise.reject(
                    "System Error. Please try again later."
                  );
                }
                if (res) {
                  return Promise.reject(
                    typeof res === "object" ? JSON.stringify(res) : res
                  );
                }
                return Promise.reject(response.statusText);
              }
              if (response.status === 204) {
                return response;
              }
              return response.json();
            } else {
              return Promise.reject("Request failed");
            }
          }
        } catch (err) {
          return Promise.reject(err);
        }
      })
      .then((data) => {
        // console.log(data);
        if (data.code) {
          if (data.code === 401) {
            resetLocalToken();
          }
          if (data.code < 200 && data.code >= 300) {
            return reject(data.desc ?? data.message ?? "Request failed");
          }
        }
        return resolve(data);
      })
      .catch((err) => {
        if (didTimeOut) return;
        return reject(err);
      });
  });
}

export function authApi(url: string, params: any, method = "POST") {
  let didTimeOut = false;

  return new Promise(function (resolve, reject) {
    const timeout = setTimeout(function () {
      didTimeOut = true;
      return reject(new Error("Request timed out"));
    }, FETCH_TIMEOUT);

    fetch(url, {
      mode: "cors", // 'cors - no-cors'
      method,
      body: typeof params === "object" ? JSON.stringify(params) : params,
      headers: new Headers({
        accept: "application/json",
        "content-type": "application/json",
      }),
    })
      .then(async (response) => {
        clearTimeout(timeout);
        if (didTimeOut) {
          return;
        }
        if (response == null) {
          return;
        }
        if (response.status < 200 || response.status >= 300) {
          const contentType = response.headers.get("content-type") ?? "";
          if (contentType.indexOf("json") >= 0) {
            const res = await response.json();
            throw new Error(
              typeof res === "object" ? JSON.stringify(res) : res
            );
          } else {
            throw new Error(response.statusText);
          }
        } else return response.json();
      })
      .then((data) => {
        if (data.code === 401) {
          resetLocalToken();
        }
        // fix token is old token at base api
        if (data.token) {
          token = getLocalToken() ?? "";
        }
        return resolve(data);
      })
      .catch((err) => {
        if (didTimeOut) return;
        return reject(err);
      });
  });
}

// for call api with attack file
export function uploadFormData(
  url: string,
  formData: FormData,
  method = "POST",
  onProgress: Function
) {
  if (token) {
    if (onProgress) {
      return uploadApiWithProgress(url, formData, undefined, onProgress);
    } else return uploadNormal(url, formData, method);
  } else return Promise.reject("Not exists token");
}

export function uploadApi(
  url: string,
  fileObject: any,
  fileName: string,
  method = "POST",
  params: any,
  onProgress: Function
) {
  if (token) {
    const data = new FormData();
    if (fileObject instanceof Array) {
      fileObject.forEach((file) => {
        data.append(fileName, file);
      });
    } else if (fileObject) {
      data.append(fileName, fileObject);
    }
    Object.keys(params).map(function (key) {
      data.append(key, params[key]);
    });
    if (onProgress) {
      return uploadApiWithProgress(url, data, undefined, onProgress);
    } else return uploadNormal(url, data, method);
  } else return Promise.reject("Not exists token");
}
// base upload func
function uploadNormal(url: string, data: FormData, method = "POST") {
  if (!token) {
    token = getLocalToken() ?? "";
  }
  let didTimeOut = false;

  return new Promise(function (resolve, reject) {
    const timeout = setTimeout(function () {
      didTimeOut = true;
      return reject(new Error("Request timed out"));
    }, FETCH_TIMEOUT);

    fetch(url, {
      mode: "cors",
      method,
      headers: {
        // 'Accept': 'application/json',
        // 'content-type': 'application/x-www-form-urlencoded',
        Authorization: token,
        // credentials: 'same-origin',
      },
      body: data,
    })
      .then(async (response) => {
        clearTimeout(timeout);
        if (!didTimeOut) {
          if (response.status < 200 || response.status >= 300) {
            let contentType = response.headers.get("content-type") ?? "";
            if (contentType.indexOf("json") >= 0) {
              const res = await response.json();
              throw new Error(JSON.stringify(res));
            } else {
              throw new Error(response.statusText);
            }
          } else return response.json();
        }
      })
      .then((data) => {
        return resolve(data);
      })
      .catch((err) => {
        if (didTimeOut) return;
        return reject(err);
      });
  });
}

function uploadApiWithProgress(
  url: string,
  data: FormData,
  method = "POST",
  onProgress: Function
) {
  if (data instanceof FormData) {
    return new Promise((res, rej) => {
      var xhr = new XMLHttpRequest();
      xhr.open("post", url);
      xhr.setRequestHeader("x-api-key", token);
      xhr.setRequestHeader("mode", "cors");
      xhr.onload = (e) => res(e.target);
      xhr.onerror = rej;
      if (xhr.upload && onProgress) {
        // @ts-ignore
        xhr.upload.onprogress = onProgress; // event.loaded / event.total * 100 ; //event.lengthComputable
      }
      xhr.send(data);
    });
  } else return Promise.reject("Data not form data");
}
